.wrapper {
  place-self: center;
  max-width: 90%;
}

.wrapper .holder {
  background-color: var(--primary);
  border: 1px solid var(--accent);
  grid-template-rows: auto 1fr auto;
  width: fit-content;
  min-width: 50vw;
  min-height: 50vh;
  padding: 30px;
  display: grid;
}

.holder > :not(:first-child) {
  padding-top: 20px;
}

.holder button {
  width: 100px;
  height: 30px;
}

.table {
  text-align: center;
}

.table .row:first-child {
  border-top: 2px solid var(--accent);
  border-bottom: 4px solid var(--accent);
  font-weight: bold;
}

.table .row {
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

.row > :first-child {
  border-left: 2px solid var(--accent);
}

.row > * {
  margin: 0;
  margin: fit-content;
  border: 2px solid var(--accent);
  border-top: 0;
  border-left: 0;
  padding: 10px;
}

.tableActions {
  grid-template-columns: auto 1fr auto;
  display: grid;
}

.tableActions label {
  align-items: center;
  display: flex;
}

.tableActions input:is([type="text"], [type="password"]) {
  color: #fff;
  background-color: var(--secondary);
  border: 1px solid var(--accent);
  width: 20ch;
  margin: 10px;
  font-size: 20px;
}

@media (width <= 800px) {
  .tableActions input:is([type="text"], [type="password"]) {
    width: 15ch;
    font-size: 20px;
  }
}

.holder button {
  text-align: center;
  width: 40px;
  height: 40px;
  padding: 4px;
}

.tableActions svg {
  border-bottom: 2px solid #fff;
}

.tableActions button:hover svg {
  border-bottom: 2px solid var(--accent);
}

.form .holder {
  background-color: var(--primary);
  border: 1px solid var(--accent);
  width: fit-content;
  padding: 30px;
}

.form input[type="text"] {
  color: #fff;
  background-color: var(--secondary);
  border: 1px solid var(--accent);
  width: 20ch;
  margin: 10px;
  font-size: 20px;
}

@media (width <= 800px) {
  .form input:is([type="text"], [type="password"]) {
    width: 15ch;
  }
}

.form button {
  width: 100px;
  height: 30px;
}

.form .edit {
  text-align: right;
}

.form .submit {
  text-align: center;
  margin-top: 20px;
  padding: 0;
}

.error {
  color: #ff3434;
  font-weight: bold;
  transition: scale .2s;
  scale: 100%;
}

.error:empty {
  scale: 0%;
}

.inactive {
  display: none;
}
